import {callbackErrorHandler} from '../lib/util.js';
import { includeReview } from '../lib/util.js';

import appSettings from '../settings.js';

import settings_domain_list_tmpl from '../../html/settings_domain_list.njk'



var browser;

if (!browser) {
    browser = chrome;
}

function loadBlockedDomains() {

    // function to populate the paused domains list

    // send message to background to get blocked domains
    browser.runtime.sendMessage({
        'subject': 'getBlockedDomains',
    }, callbackErrorHandler(function (blockedDomains) {

        let minuteOptions = []
        for (let i = 0; i<=500; i+=5) {
            minuteOptions.push(i);
        }

        let rendered = settings_domain_list_tmpl({
            blockedDomains,
            minuteOptions,
        });


        // put the rendered html in the domain list element
        document.getElementById('domain-list').innerHTML = rendered;


        addEventListeners();
        // loop through all the elements and add an on click listener
        for (let domainRow of document.querySelectorAll('.domain-row')) {

            // on click on the domain, send a message to the background to remove it from storage
            // and re-render the whole list again
            domainRow.addEventListener('click', function(e) {
                e.preventDefault();

                let domain = domainRow.getAttribute('data-domain');
                // are you sure pop-up on remove click, to prevent accidental clicks
                if (!confirm('Are you sure you want to delete "'+domain+'" from pause list?')){
                    return;
                }

                browser.runtime.sendMessage({
                    subject: 'removeBlockedDomain',
                    domain,
                }, callbackErrorHandler(function(){
                    loadBlockedDomains();
                }));
            });
        }
    }));
}

function loadIsShowTimer() {
    browser.runtime.sendMessage({
        'subject': 'getIsShowTimer',
    }, callbackErrorHandler( function(isShowTimer){
        document.getElementById("limit-timer").checked = isShowTimer;
    }));
}

function setIsShowTimer(isShowTimer) {
    browser.runtime.sendMessage({
        subject: 'setIsShowTimer',
        isShowTimer,
    });



}

function addEventListeners () {
    Array.from(document.getElementsByClassName('limit-input')).forEach(function(element) {
        element.addEventListener('change', function(e){
            setLimitForDomain(this.getAttribute('data-domain'), this.value);
        });
    });


}

var timeoutFormMessage = {};

var setLimitForDomain = function(domain, limit){

    let successFormMessageId = 'limit-success-message-'+domain;
    let errorFormMessageId = 'limit-error-message-'+domain;

    //clearTimeout(timeoutFormMessage[formMessageId]);
    cleanFormMessage(successFormMessageId);
    cleanFormMessage(errorFormMessageId, true);


    browser.runtime.sendMessage({
        subject: 'setLimitForDomain',
        domain,
        limit,
    }, callbackErrorHandler( function(result) {
        if (!result.error) {
            document.getElementById(successFormMessageId).innerHTML = 'Saved';
            document.getElementById(successFormMessageId).classList.add('success');
            // clear message after 5 seconds
            setTimeout(function(){
                cleanFormMessage(successFormMessageId);
            },5000);
        } else {
            document.getElementById(errorFormMessageId).innerHTML = result.error;
            document.getElementById(errorFormMessageId).classList.add('error');
            document.getElementById(errorFormMessageId).classList.remove('hidden');
        }
    }));
};

// clear error and success messages
function cleanFormMessage(messageElementId, hide=false) {
    let formMessageElement = document.getElementById(messageElementId);
    if(hide){
        formMessageElement.classList.add('hidden');
    }
    formMessageElement.innerHTML = "";
    formMessageElement.classList.remove('error');
    formMessageElement.classList.remove('success');
}

// on load attach event listeners
window.onload = function(){
    includeReview();
    loadBlockedDomains();
    loadIsShowTimer();

    // on input change clean the form messages
    document.querySelector("#new-domain").addEventListener("input", function(e){
        cleanFormMessage('add-domain-form-message');
    });

    // on form submit, send a message to background to add the new domain in storage
    // and re-render the list
    // show success/error messages accordingly
    document.querySelector("#add-domain-form").addEventListener("submit", function(e){
        e.preventDefault();

        cleanFormMessage('add-domain-form-message');
        let newDomain = document.getElementById('new-domain').value.trim().toLowerCase();

        if (newDomain == "") {
            return;
        }

        browser.runtime.sendMessage({
            'subject': 'addBlockedDomain',
            'domain': newDomain
        }, callbackErrorHandler( function(result) {
            loadBlockedDomains();

            // show success or error messages
            if (!result.error) {
                document.getElementById('new-domain').value = "";
                document.getElementById('add-domain-form-message').innerHTML =
                    newDomain+' was successfully added to the pause list';
                document.getElementById('add-domain-form-message').classList.add('success');

                // clear message after 5 seconds
                setTimeout(function(){
                    cleanFormMessage('add-domain-form-message');
                },5000);
            } else {
                document.getElementById('add-domain-form-message').innerHTML = result.error;
                document.getElementById('add-domain-form-message').classList.add('error');
            }
        }));
    });

    // for some unknwon reason .addEventListener('change') will not work
    // so I am using the old onchange property
    document.getElementById("limit-timer").onchange = function(e) {

        setIsShowTimer(document.getElementById("limit-timer").checked);
        document.getElementById('limit-timer-form-message').innerHTML = 'Saved!';
        document.getElementById('limit-timer-form-message').classList.add('success');
        setTimeout(function(){
            cleanFormMessage('limit-timer-form-message');
        },5000);
    };
};
